import template from './dao-input.html';
import './dao-input.scss';

function DaoInputContainerDirective() {
  'ngInject';

  return {
    restrict: 'E',
    scope: {
      error: '=',
      success: '=',
      warning: '=',
      message: '=',
      search: '@',
    },
    // scope: false,
    // controller: DaoInputContainerController,
    template,
    replace: true,
    transclude: true,
  };
}

